@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:400');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

:root {
  --primary-color: #2541B2;
  --secondary-color: #757d92;
  --header-fonts: Hind, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --mono-fonts: 'SF Mono', 'Roboto Mono', Inconsolata, Consolas, Menlo, monospace;
  --fonts: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

@page {
  size: A4;
  margin: 0;
}

@media print {
  html, body {
    width: 210mm;
    height: 297mm;
    font-size: 13px;
    color: #333 !important;
  }

  h2, h4, h5, h6 {
    color: #000 !important;
  }

  /* ... the rest of the rules ... */
  .Project .skill {
    font-size: 0.6rem !important;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--fonts);
  font-weight: 400;
  font-size: 15px;
  color: #444;
  background: rgb(250, 250, 255);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--header-fonts);
  color: #333;
}

p {
  margin-top: 4px;
  margin-bottom: 10px;
  font-size: 0.8rem;
  line-height: 1.3rem;;
}

h1, h3 {
  text-transform: uppercase;
  font-weight: normal;
}

h3 {
  display: -ms-flexbox;
  display: flex;
  white-space: nowrap;
  font-size: 1rem;
  color: var(--primary-color);
}

h4 {
  font-size: 0.88rem;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
}

@media screen and (max-width: 768px) {
  .row {
    -ms-flex-direction: column!important;
        flex-direction: column!important;
  }
}

.bold {
  font-weight: bold;
  margin-right: 2px;
}

.left-section {
  -ms-flex: 1 1;
      flex: 1 1;
  background: url("data:image/svg+xml;base64,");
