.left-section .section {
  padding: 12px 16px;
  page-break-inside: avoid;
}

.right-section {
  -ms-flex: 4 1;
      flex: 4 1;
  -webkit-box-shadow: -4px 0 4px #eee;
          box-shadow: -4px 0 4px #eee;
  background: #fff;
}

.right-section .section {
  padding: 10px 32px;
}

.right-section .section:first-child {
  padding-top: 16px;
}

.footer {
  padding-top: 4px;
}

.footer p {
  text-align: center;
  font-size: 0.7rem;
  margin: 0;
}

.footer .skill {
  padding: 0 3px;
  border: 1px solid #eee;
}

@media screen {
  .Resume_container {
    max-width: 960px;
    margin: 0 auto;
