}

.roleName, .timeline, .school, .title, .skillGroupName, .presenter, .oss-project, .stack {
  margin-top: 2px;
  font-size: 0.7rem;
  color: var(--secondary-color);
  text-transform: uppercase;
  font-weight: bold;
}

.stack {
  margin-right: 8px;
  margin-top: 4px;
  font-size: 0.6rem;
}

.timeline {
  font-size: 0.7rem;
