.about {
  padding: 12px 16px;
  padding-bottom: 24px;
  background: var(--primary-color);
  position: relative;
  color: #fff;
}

.meta {
  list-style: none;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}

.meta li {
  font-family: var(--mono-fonts);
